body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
  background-color: #575656;
  height: 100vh;
}

#root {
  display: flex;
  flex-direction: column;
  height: 100%;
}

header {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  padding: 5px;
  background-color: rgb(24, 24, 24);
  padding-bottom: 15px;
  border-bottom: 1px solid rgb(0, 0, 0);
  justify-content: center;
}

header img {
  width: 100px;
  height: auto;
  padding: 5px;
  margin: 10px;
  margin-bottom: 0px;
}

header input {
  box-sizing: border-box;
  height: 35px;
  margin-top: 10px;
  margin-right: 10px;
  border-radius: 35px;
  border: none;
  background-color: #f3f3f3;
  padding-left: 15px;
  flex: 1;
}

input:focus-visible {
  border: 0.5px solid gray;
}

input:focus-within,
input:hover {
  border: 0.5px solid gray;
  outline: none;
}

nav ul {
  user-select: none;
  list-style-type: none;
  margin: 0px;
  margin-top: 10px;
  padding: 0;
}

nav ul li {
  display: inline;
  padding: 5px;
  border-radius: 4px;
  margin: auto;
}

nav ul li:hover {
  background-color: #363636;
}

nav ul li:active {
  background-color: #555555;
}

nav ul li a {
  color: rgb(207, 206, 206);
  text-decoration: none;
  font-size: 12px;
}

.outdiv {
  height: 100%;
  background-color: #363636;
  margin: 5px;
  border-radius: 10px;
  padding: 10px;
  box-sizing: border-box;
  color: white;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

.card {
  background-color: #181818;
  margin: 5px;
  width: 350px;
  height: fit-content;
  border-radius: 10px;
  padding: 10px;
  box-shadow: 0px 3px 5px 0px rgb(36, 36, 36);
  overflow: hidden;
  border-top: 1px solid gray;
}

.settings {
  width: 20px;
  height: 20px;
  border-radius: 3px;
  padding: 5px;
  cursor: pointer;
  float: right;
  position: relative;
  display: flex;
  justify-content: center;
}

.settings:hover {
  background-color: #575656;
}
.settings:active {
  background-color: #333333;
}

.settings span {
  width: 4px;
  height: 4px;
  display: block;
  border-radius: 50%;
  background-color: white;
  position: absolute;
}

.settings span:nth-child(1) {
  top: 7px;
}
.settings span:nth-child(2) {
  top: 13px;
}
.settings span:nth-child(3) {
  top: 19px;
}

h2 {
  font-family: sans-serif;
}

h2,
p {
  margin: 15px;
  color: white;
}

.card p {
  font-size: 14px;
  color: #d3d3d3;
  line-height: 1.2rem;
  max-height: 2.4rem;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: normal;
}

p strong {
  color: white;
}

.fotos {
  display: flex;
  width: 100%;
  justify-content: center;
}

img {
  user-select: none;
  border-radius: 10px;
  margin: 5px;
  width: 100px;
  height: 100px;
}

.loading {
  width: 40px;
  height: 40px;
  border: 4px solid #f3f3f3;
  border-top: 4px solid #3498db;
  border-radius: 50%;
  animation: spin 2s linear infinite;
  margin: auto;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
